.container-work {
  padding: 10px 16px 20px 40px;
}

.title-work {
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin-bottom: 10px;

  font-size: 14px;
  font-weight: 700;
  line-height: 1.9;

  & .title-work-text {
    min-width: 240px;
    font-size: 22px;
    font-weight: 700;
    text-shadow: 1px 1px 3px grey, 1px 1px 1px red;
  }

