.title-name {
  margin-bottom: 18px;
  font-size: 47px;
  font-weight: 700;

  text-shadow: 4px 4px 5px grey, 2px 2px 1px red;
}

.personal-information {
  border-top: 1px solid var(--marker-list-color);
  border-bottom: 1px solid var(--marker-list-color);
}

.list-information {
  color: var(--block-info-text-color);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.9;
}

.additional-information {
  margin-top: 10px;
  margin-bottom: 16px;

  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  margin-top: 1.9;
}
