.tooltiptext {
  visibility: hidden;
  min-width: 120px;
  color: #fff;
  text-align: center;
  z-index: 10;
  opacity: 0;
  background-color: #555;
  border-radius: 6px;
  margin-left: -60px;
  padding: 5px;
  transition: opacity .3s;
  position: absolute;
  bottom: 125%;
  left: 50%;
}

.icon-email + .tooltiptext {
  margin-left: -90px;
}

.tooltiptext:after {
  content: "";
  border: 5px solid rgba(0, 0, 0, 0);
  border-top-color: #555;
  margin-left: -5px;
  position: absolute;
  top: 100%;
  left: 50%;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

